import React, { useCallback, useState } from "react";
import { colors } from "../../../assets/color/color";
import Header from "../../component/HeaderComponent";
import { GridList, Image, ListItem, SkeletonView, Text, View } from "react-native-ui-lib";
import { FlatList, SafeAreaView, TouchableOpacity } from "react-native";
import { scale, width } from "../../contanst";
import { useFocusEffect } from "@react-navigation/native";
import { getByServiceId } from "../../api/ticketApi";
import { image_detail, image_empty } from "../../../image";
import { FontFamily } from "../../global";
import i18n from "../../../i18n";

const ServiceByIdComponent = (props: any) => {
  const { navigation, route } = props;
  const [dataTicket, setDataTicket] = useState<any>();

  useFocusEffect(useCallback(() => {
    (async () => {
      const data = await getByServiceId(route?.params?.serviceId)
      setDataTicket(data);
    })()
  }, []))

  const renderItemTicket = ({ item, index }: any) => {
    return (
      <TouchableOpacity
        onPress={() => navigation.navigate("webviewCreateTicket", { dataTicket: item })}
        style={{
          flexDirection: "row",
          alignItems: "center",
          justifyContent: "space-between",
          paddingHorizontal: 16,
          paddingVertical: 13,
          borderBottomColor: index < dataTicket?.length - 1 ? "rgba(0, 53, 128, 0.15)" : "unset",
          borderBottomWidth: index < dataTicket?.length - 1 ? 0.75 : 0
        }}>
        <Text>{item.flowName}</Text>
        <Image width={16} height={16} source={image_detail} />
      </TouchableOpacity>
    )
  }

  if (dataTicket && dataTicket.length == 0) {
    return (
      <View style={{ flex: 1, position: "relative" }}>
        <Header containerStyle={{
          height: "15%",
        }} backgroundColor={colors.primary} color={'#fff'} label={route?.params?.serviceName} {...props} />

        <View
          style={{
            backgroundColor: "rgba(255, 255, 255, 1)",
            position: "absolute",
            top: "13%",
            borderTopLeftRadius: 17,
            borderTopRightRadius: 17,
            height: "88%",
            width: "100%",
            alignItems: "center",
            justifyContent: "center"
          }}>
          <Image source={image_empty} width={95} height={110} />
          <Text style={{ marginTop: 25, fontSize: 14, fontWeight: "400", fontFamily: FontFamily.interMedium }}>{i18n.t('main.nodata')}</Text>
        </View>
      </View>
    )
  }

  return (
    <View style={{ flex: 1, position: "relative" }}>
      <Header containerStyle={{
        height: "15%",
      }} backgroundColor={colors.primary} color={'#fff'} label={route?.params?.serviceName} {...props} />

      <View
        style={{
          backgroundColor: "rgba(255, 255, 255, 1)",
          position: "absolute",
          top: "13%",
          borderTopLeftRadius: 17,
          borderTopRightRadius: 17,
          height: "88%",
          width: "100%"
        }}>


        <SafeAreaView style={{
          marginTop: 61,
          height: "84%",
        }}>

          {dataTicket ? (
            <FlatList
              data={dataTicket}
              renderItem={renderItemTicket}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
              numColumns={1}
            />
          ) : (
            <SkeletonView
              template={SkeletonView.templates.LIST_ITEM}
              showContent={true}
              times={10}
            />
          )}
        </SafeAreaView>
      </View>
    </View>
  )
}

export default ServiceByIdComponent;
