import { createNativeStackNavigator } from "@react-navigation/native-stack";
import React from "react";
import MainScreen from "../screen/MainScreen";
import ServiceByIdComponent from "../screen/Services/ServiceByIdComponent";
import WebviewCreateTicket from "../screen/Services/WebviewCreateTicket";
import SuccessCreateContract from "../screen/Services/CreateTicketSuccess";


const Main = createNativeStackNavigator();

const MainNavigation = () => {
  return (
    <Main.Navigator
      screenOptions={{
        headerShown: false
      }}
      initialRouteName="main"
    >
      <Main.Screen name="main" component={MainScreen} />
      <Main.Group screenOptions={{ headerShown: false }}>
        <Main.Screen name="serviceById" component={ServiceByIdComponent} />
        <Main.Screen name="webviewCreateTicket" component={WebviewCreateTicket} />
        <Main.Screen name="successCreateTicket" component={SuccessCreateContract} />
      </Main.Group>
    </Main.Navigator>
  )
}

export default MainNavigation;