import { Dimensions, PixelRatio, Platform } from "react-native";

export const BASE_URL_EACCOUNT = 'http://10.14.121.10/eaccount/'
export const BASE_URL_EREQUEST = 'http://10.14.121.10/erequest/services/erequest/'
export const BASE_URL_EFLOW = 'http://10.14.121.10/erequest/services/idaas-eflow/'
export const WEB_VIEW_FORM = 'http://10.14.121.10/eform/preview-template/'
export const TOKEN = 'token'
export const INFO_USER = 'info_user'
export const BASE_64 = "data:image/png;base64,"

const guidelineBaseWidth = 390;
const { width, height } = Dimensions.get('window');
export const scale = function (size: number) {
  if (Platform.OS === 'ios') {
    if (Platform.isPad) {
      return Math.round((width / guidelineBaseWidth) * size);
    }
    return Math.round(
      PixelRatio.roundToNearestPixel((width / guidelineBaseWidth) * size),
    );
  } else {
    return Math.round(PixelRatio.roundToNearestPixel(size));
  }
};

export { width, height }
