import React, { useCallback, useEffect, useRef, useState } from "react";
import { SafeAreaView, StyleSheet } from "react-native";
import Header from "../../component/HeaderComponent";
import { Button, ButtonSize, View } from "react-native-ui-lib";
import { colors } from "../../../assets/color/color";
import SplashScreen from "../../component/SplashScreen";
import WebView from "react-native-webview";
import { WEB_VIEW_FORM } from "../../contanst";
import { useFocusEffect } from "@react-navigation/native";
import { createContract, getModeler } from "../../api/ticketApi";
import { dataPriority } from "../../data";
import i18n from "../../../i18n";

const WebviewCreateTicket = (props: any) => {
  const { navigation, route } = props;
  const webview = useRef<any>(null);
  const [formVersion, setFormVersion] = useState()
  const [modelerData, setModelerData] = useState<any>()
  const [disable, setDisable] = useState(false)
  const [loading, setLoading] = useState(false)
  const [priority, setPriority] = useState(dataPriority[0])
  const [textTitle, changeTextTitle] = useState()
  const [modalVisible, setModalVisible] = React.useState(false);

  useFocusEffect(useCallback(() => {
    (async () => {
      const data = await getModeler(route?.params?.dataTicket?.modelerId)
      setModelerData(data)
      setFormVersion(data.formVersion)
    })()
  }, []))

  const onMessage = (event: any) => {
    const res = JSON.parse(event.nativeEvent.data);
    if (res.result == 'result') {
      setLoading(true);
      const dataSelect = route?.params?.dataTicket
      if (dataSelect && modelerData) {
        const dataBody = {
          followerId: modelerData.followerId,
          formData: [
            {
              formData: JSON.stringify(res.data),
              ticketId: null,
              userTaskId: null,
              version: formVersion
            }
          ],
          jsonXml: modelerData.jsonXml,
          listRelativeTicket: [],
          priority: priority.priority,
          ticketStatus: 'processing',
          title: textTitle,
          workFlowId: dataSelect.id
        }
        createContract(dataBody)
          .then((res) => {
            if (res) {
              navigation.navigate('successCreateTicket');
            }
          })
          .catch((e) => console.log(e))
          .finally(() => setLoading(false))
      }
    }
  }

  const handleSubmitForm = (typeSubmit: string) => {
    if (textTitle === undefined || textTitle === null || textTitle === "") {
      setModalVisible(true)
      return;
    }
    const data = JSON.stringify(
      {
        result: "submit",
      }
    )
    const scriptJSX = `window.postMessage('${data}', '*');`;
    webview?.current?.injectJavaScript(scriptJSX)
  }
  return (
    <SafeAreaView style={{ flex: 1, backgroundColor: "#fff" }}>
      <Header
        changeTextTitle={changeTextTitle}
        textTitle={textTitle}
        priority={priority}
        setPriority={setPriority}
        showRightMenu={true}
        backBo={true}
        label={i18n.t('main.headerLoan')}
        setModalVisible={setModalVisible}
        modalVisible={modalVisible}
        {...props} />
      {formVersion && (
        <>
          <View style={{
            flex: 1,
            backgroundColor: "#F8FAFE"
          }}>
            <WebView
              containerStyle={{
                marginHorizontal: 18,
                marginVertical: 18,
                borderRadius: 17
              }}
              ref={webview}
              startInLoadingState
              renderLoading={() => <SplashScreen />}
              style={{ flex: 1 }}
              onMessage={onMessage}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
              autoManageStatusBarEnabled={true}
              bounces={false}
              originWhitelist={["*"]}
              scalesPageToFit={true}
              mixedContentMode={"always"}
              allowsInlineMediaPlayback={true}
              allowsFullscreenVideo={true}
              allowsBackForwardNavigationGestures={true}
              allowsLinkPreview={false}
              sharedCookiesEnabled={true}
              domStorageEnabled={true}
              javaScriptEnabled={true}
              javaScriptCanOpenWindowsAutomatically={true}
              source={{ uri: WEB_VIEW_FORM + formVersion }}
            />
          </View>
          <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "space-around", flex: 0.1 }}>
            <Button onPress={() => navigation.goBack()} style={{ ...styles.btn, backgroundColor: "#F2F5F8" }} labelStyle={{ ...styles.labelStyle, color: "rgba(0, 32, 77, 0.90)" }} label={i18n.t("main.cancel")} size={ButtonSize.medium} />
            <Button
              onPress={() => handleSubmitForm('doing')}
              style={{
                ...styles.btn,
                backgroundColor: disable ? "#A17CBC" : colors.primary
              }}
              labelStyle={{ ...styles.labelStyle, color: "#fff" }} label={i18n.t("main.submit")} size={ButtonSize.medium} />
          </View>
        </>
      )}
      {loading && <SplashScreen />}
    </SafeAreaView>
  )
}

const styles = StyleSheet.create({
  btn: {
    borderRadius: 8,
    minWidth: 160
  },
  labelStyle: {
    fontSize: 14,
    fontWeight: "500"
  }
})

export default WebviewCreateTicket;