import React, { useState } from "react";
import { StyleSheet } from "react-native";
import { Image, Text, TouchableOpacity, View } from "react-native-ui-lib";
import { colors } from "../../assets/color/color";
import { image_back, image_backBo, image_hierarchy, image_mail, image_more_hoz, image_person_user, image_phone, image_swap } from "../../image";
import { Actionsheet, Box, HamburgerIcon, Menu, Pressable, useDisclose } from "native-base"
import { dataPriority } from "../data";
import ModalComponent from "./ModalComponent";
import { BASE_64, height } from "../contanst";
import { getAccount } from "../api/authApi";
import i18n from "../../i18n"

const Header = (props: any) => {
  const { isOpen, onOpen, onClose } = useDisclose();
  const [user, setUser] = useState<any>();
  const containerStyle = props.containerStyle && {
    ...props.containerStyle
  }
  const style = props.labelStyle ? {
    ...props.labelStyle
  } : undefined

  const renderActionSheet = () => {
    return (
      <Actionsheet style={{}} isOpen={isOpen} onClose={onClose}>
        <Actionsheet.Content style={{ height: 300, marginTop: 20, alignItems: "flex-start", rowGap: 20 }}>
          {user && (
            <>
              <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 10, paddingHorizontal: 10 }}>
                <Image width={50} height={50} borderRadius={25} source={{ uri: BASE_64 + user.avatar }} />
                <Text>{"" + user.firstName + user.lastName}</Text>
              </View>
              <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 10, marginLeft: 40 }}>
                <Image width={24} height={24} source={image_mail} />
                <Text>{"" + user.email}</Text>
              </View>
              <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 10, marginLeft: 40 }}>
                <Image width={24} height={24} source={image_hierarchy} />
                <Text>{"" + user.acNameOrg}</Text>
              </View>
              <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 10, marginLeft: 40 }}>
                <Image width={24} height={24} source={image_phone} />
                <Text>{"" + user.phone}</Text>
              </View>
            </>
          )}
        </Actionsheet.Content>
      </Actionsheet>
    )
  }

  const handleOpenUserCreate = async () => {
    const dataUser = await getAccount();
    setUser(dataUser)
    onOpen()
  }

  return (
    <>
      <View style={{ flexDirection: "row", width: "100%", ...containerStyle, alignItems: "center", paddingTop: 15, paddingBottom: 15 }} backgroundColor={props.backgroundColor ? props.backgroundColor : "#fff"}>
        <TouchableOpacity onPress={() => {
          props.onPressBackButton === undefined
            ? props.navigation.goBack()
            : props.onPressBackButton();
        }}
          style={{ position: "absolute", left: 24, zIndex: 1, paddingTop: 15 }}
        >
          <Image width={24} height={24} source={props.backBo ? image_backBo : image_back} />
        </TouchableOpacity>
        <View style={{ width: "100%" }}>
          <Text color={props.color ? props.color : colors.colorTitie} style={style ? style : styles.styleLabel}>{props?.label}</Text>
        </View>
        {props.showRightMenu && (
          <View
            style={{ position: "absolute", right: 4, zIndex: 1, paddingTop: 15 }}
          >
            <View style={{ flexDirection: "row", alignItems: "center", justifyContent: "center", columnGap: 8 }}>
              <Menu style={{ marginRight: 20, marginTop: 10, borderRadius: 10 }} w="60" trigger={triggerProps => {
                return <TouchableOpacity
                  style={{
                    borderWidth: 0.75,
                    borderColor: "#A17CBC",
                    paddingHorizontal: 4,
                    paddingVertical: 4,
                    borderRadius: 4,
                    flexDirection: "row",
                    alignItems: "center",
                    columnGap: 4
                  }}
                  accessibilityLabel="More options menu" {...triggerProps}>
                  <Image width={12} height={12} source={props?.priority.imageSrc} />
                  <Image style={{ transform: [{ rotate: "-90deg" }] }} width={12} height={12} source={image_backBo} />
                </TouchableOpacity>;
              }}>
                {dataPriority.map((item) =>
                  <Menu.Item key={item.priority} onPress={() => props.setPriority(item)}>
                    <Image width={12} height={12} source={item.imageSrc} />
                  </Menu.Item>)}
              </Menu>
              <Menu style={{ marginRight: 20, marginTop: 10, borderRadius: 10 }} w="200" trigger={triggerProps => {
                return <Pressable accessibilityLabel="More options menu" {...triggerProps}>
                  <Image width={24} height={24} source={image_more_hoz} />
                </Pressable>;
              }}>
                <Menu.Item onPress={handleOpenUserCreate} style={{ flexDirection: "row", alignItems: "center", justifyContent: "space-between", paddingHorizontal: 0 }}>
                  <Image width={20} height={20} source={image_person_user} />
                  <Text>{i18n.t("main.info")}</Text>
                </Menu.Item>
                <Menu.Item isDisabled style={{ flexDirection: "row", alignItems: "center", justifyContent: "space-between", opacity: 0.5 }}>
                  <Image width={20} height={20} source={image_swap} />
                  <Text>{i18n.t("main.ticketRelative")}</Text>
                </Menu.Item>
              </Menu>
            </View>
          </View>
        )}
      </View>
      {props.modalVisible && (
        <ModalComponent
          modalVisible={props.modalVisible}
          setModalVisible={props.setModalVisible}
          setChangeText={props.changeTextTitle}
          changeText={props.textTitle}
        />
      )}
      {isOpen && (
        renderActionSheet()
      )}
    </>
  )
}

const styles = StyleSheet.create({
  styleLabel: {
    fontSize: 18,
    fontWeight: "600",
    textAlign: "center"
  }
})

export default Header;