import AsyncStorage from "@react-native-async-storage/async-storage";
import { BASE_URL_EACCOUNT, TOKEN } from "../contanst"

export const loginApi = async (body: any) => {
  const respones = await fetch(BASE_URL_EACCOUNT + 'auth/login',
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(body)
    }
  )
    .then((res) => res.json())
    .catch((e) => console.log(e))

  return respones;
}

export const forgetPassword = async (body: any) => {
  const respones = await fetch(BASE_URL_EACCOUNT + '',
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(body)
    }
  )
    .then((res) => res.json())
    .catch((e) => console.log(e))

  return respones;
}

export const getAccount = async () => {
  const token = await AsyncStorage.getItem(TOKEN)
  const respones = await fetch('http://10.14.121.10/eform/services/uaa/api/account',
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`
      },
    }
  )
    .then((res) => {
      if (res.status === 200 || res.status === 201) {
        return res.json();
      } else {
        return null
      }
    })
    .catch((e) => console.log(e))

  return respones;
}