import React, { useCallback, useEffect, useState } from "react";
import { GridList, Image, SkeletonView, Text, View } from "react-native-ui-lib";
import Header from "../component/HeaderComponent";
import { colors } from "../../assets/color/color";
import { SafeAreaView, StyleSheet, TouchableOpacity } from "react-native";
import InputComponent from "../component/InputComponent";
import { BASE_64, INFO_USER, TOKEN, scale, width } from "../contanst";
import { FontFamily } from "../global";
import { image_default_service, image_eye } from "../../image";
import { useFocusEffect } from "@react-navigation/native";
import { searchServices } from "../api/ticketApi";
import AsyncStorage from "@react-native-async-storage/async-storage";
import { useTranslation } from "react-i18next";
import i18n from "../../i18n"

const MainScreen = (props: any) => {
  const { navigation, route } = props;
  const [text, onChangeSearch] = useState<string>('')
  const [dataService, setDataService] = useState<any>();

  useFocusEffect(useCallback(() => {
    (async () => {
      const data = await searchServices('');
      setDataService(data.content);
      await handleGetInitLanguage();
    })()
  }, []))

  const handleGetInitLanguage = async () => {
    const dataUser = await AsyncStorage.getItem(INFO_USER)
    if (dataUser) {
      const language = JSON.parse(dataUser).langKey
      i18n.changeLanguage(language)
    } else {
      i18n.changeLanguage('vi')
    }
  }

  const renderItemCard = ({ item }: any) => {
    return (
      <TouchableOpacity onPress={() => navigation.navigate("serviceById", { serviceId: item.id, serviceName: item.serviceName })} style={styles.styleCard}>
        <View style={{ paddingHorizontal: 18, paddingVertical: 22 }}>
          {item.iconBase64 ? (
            <Image
              source={{ uri: BASE_64 + item.iconBase64 }}
              width={35} height={35}
              borderRadius={12}
              style={{ backgroundColor: item.color, marginBottom: 10 }} />
          ) : (
            <Image
              source={image_default_service}
              width={35} height={35}
              borderRadius={12}
              style={{ backgroundColor: item.color, marginBottom: 10 }} />
          )}
          <Text style={styles.subText}>{item.serviceName}</Text>
        </View>
      </TouchableOpacity>
    )
  }
  return (
    <View style={{ flex: 1, position: "relative" }}>
      <Header containerStyle={{
        height: "20%",
      }} backgroundColor={colors.primary} color={'#fff'} label={'eRequest'} {...props} />

      <View
        style={{
          backgroundColor: "rgba(255, 255, 255, 1)",
          position: "absolute",
          top: "18%",
          borderTopLeftRadius: 17,
          borderTopRightRadius: 17,
          height: "88%",
          width: "100%"
        }}>

        <View style={{
          top: '-3%',
          zIndex: 1,
          paddingHorizontal: '6%',
          position: 'absolute',
          width: '100%',
        }}>
          <InputComponent placeholder={i18n.t("main.search")} text={text} onChangeText={onChangeSearch} />
        </View>

        <SafeAreaView style={{
          marginTop: 61,
          paddingHorizontal: scale(30),
          height: "84%",
        }}>
          <Text style={{ ...styles.text, marginBottom: 16 }}>{i18n.t('main.title')}</Text>

          {dataService ? (
            <GridList
              data={dataService}
              renderItem={renderItemCard}
              numColumns={2}
              keyExtractor={item => item.id}
              itemSpacing={scale(20)}
              containerWidth={scale((width - 30) / 1.05)}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
            />
          ) : (
            <GridList
              data={[{}, {}, {}, {},]}
              renderItem={() => (
                <SkeletonView
                  template={SkeletonView.templates.LIST_ITEM}
                  showContent={true}
                  times={10}
                />
              )}
              numColumns={1}
              itemSpacing={scale(20)}
              containerWidth={scale((width - 30) / 1.05)}
              showsHorizontalScrollIndicator={false}
              showsVerticalScrollIndicator={false}
            />
          )}
        </SafeAreaView>
      </View>
    </View>
  )
}

const styles = StyleSheet.create({
  text: {
    fontSize: scale(17),
    fontWeight: "500",
    fontFamily: FontFamily.interMedium,
    color: "#2F394E"
  },
  subText: {
    fontSize: scale(14),
    fontWeight: "400",
    fontFamily: FontFamily.interMedium,
    color: "#2F394E"
  },
  styleCard: {
    backgroundColor: "#FBFBFB",
    borderRadius: 16,
    shadowColor: "#000000",
    shadowOffset: {
      width: 0,
      height: 3,
    },
    shadowOpacity: 0.17,
    shadowRadius: 3.05,
    elevation: 4,
    minHeight: 130
  }
})

export default MainScreen;