import AsyncStorage from "@react-native-async-storage/async-storage"
import { BASE_URL_EFLOW, BASE_URL_EREQUEST, TOKEN } from "../contanst"

export const searchServices = async (textSearch: string) => {
  const token = await AsyncStorage.getItem(TOKEN)
  const response = await fetch(BASE_URL_EREQUEST + `api/services/search?sort=displayOrder,asc&page=0&size=10000&sort=createdDate,desc&textSearch=${textSearch}`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${token}`
    },
    body: JSON.stringify({
      isAdmin: false,
      status: ["display"]
    })
  })
    .then((res) => {
      if (res.status === 200 || res.status === 201) {
        return res.json();
      } else {
        return null
      }
    })
    .catch(e => console.log(e))
  return response;
}

export const getByServiceId = async (serviceId: string) => {
  const token = await AsyncStorage.getItem(TOKEN)
  const response = await fetch(BASE_URL_EREQUEST + `api/work-flows/get-by-serviceId?serviceId=${serviceId}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      authorization: `bearer ${token}`
    }
  })
    .then((res) => {
      if (res.status === 200 || res.status === 201) {
        return res.json();
      } else {
        return null;
      }
    })
    .catch(e => console.log(e))
  return response;
}

export const getModeler = async (modelerId: string) => {
  const token = await AsyncStorage.getItem(TOKEN)
  const response = await fetch(BASE_URL_EFLOW + `api/modelers/${modelerId}`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
      "Authorization": "Bearer" + token
    }
  })
    .then((res) => {
      if (res.status === 200 || res.status === 201) return res.json();
      return null
    })
    .catch((e) => console.log(e))
  return response
}

export const createContract = async (body: any) => {
  const token = await AsyncStorage.getItem(TOKEN)
  const response = await fetch(BASE_URL_EREQUEST + `api/tickets`, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "Authorization": "Bearer" + token
    },
    body: JSON.stringify(body)
  })
    .then((res) => {
      if (res.status === 200 || res.status === 201) return res.json();
      return null
    })
    .catch((e) => console.log(e))
  return response
}
