import { Button, FormControl, Input, Modal, Text, VStack, WarningOutlineIcon } from "native-base";
import React, { useState } from "react";
import { colors } from "../../assets/color/color";
import i18n from "../../i18n"

interface IModalProp {
  modalVisible: boolean,
  setModalVisible: Function,
  changeText?: string,
  setChangeText?: any
}

const ModalComponent = (props: IModalProp) => {
  const [error, setError] = useState(false);
  const handleSubmitForm = (value: string) => {
    props.setChangeText(value)
  }
  return (
    <>
      <Modal isOpen={props.modalVisible} onClose={() => props.setModalVisible(false)} size="lg">
        <Modal.Content>
          <Modal.CloseButton />
          <Modal.Header>{i18n.t("main.nameService")}</Modal.Header>
          <Modal.Body>
            <Text textAlign={"center"}>{i18n.t("main.descriptionNoti")}</Text>
            {props.setChangeText && (
              <FormControl isInvalid={error} isRequired mt="3">
                <FormControl.Label>{i18n.t("main.title_modal")}</FormControl.Label>
                <Input onChangeText={handleSubmitForm} placeholder={i18n.t("main.placeholder")} />
                <FormControl.ErrorMessage leftIcon={<WarningOutlineIcon size="xs" />}>
                  {i18n.t("main.required_data")}
                </FormControl.ErrorMessage>
              </FormControl>
            )}
          </Modal.Body>
          <Modal.Footer>
            <Button
              backgroundColor={colors.primary}
              flex="1"
              onPress={() => {
                if (props.changeText === undefined || props.changeText == null || props.changeText == '') {
                  setError(true);
                  return;
                }
                props.setModalVisible(false);
              }}>
              {i18n.t("main.submit")}
            </Button>
          </Modal.Footer>
        </Modal.Content>
      </Modal>
    </>
  )
}

export default ModalComponent;